@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Poppins:wght@100;200;300;400;600;700&display=swap');
* {
    margin: 0;
}

.top {
    position: relative;
    width: 120%;
    height: auto;
}

.text-block {
    position: absolute;
    top: 15px;
    left: 15px;
    background-color: #FFFFFF;
    color: #222222;
    border-radius: 5px;
}

.status {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 12px;
    margin: 5px;
}

.Card {
    margin-top: 40px;
    margin-left: 36px;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;

}

.rating {
    display: flex;
    align-items: center;
}

.rating > * {
    padding: 3px;
}

.star {
    color: #FE395C;
}

.scale {
    font-weight: 300;
    color: #222222;
}

.other {
    font-weight: 300;
    color: #918E9B;
}

.title {
    font-weight: 300;
    overflow: hidden;
    text-overflow: ellipsis;

}

span {
    font-weight: 300;

}

.cardImage {
    width: 270px;
    height: auto;
}
